import React, { useEffect, useState } from "react";
import miro from "@styles/assets/images/hirescreens/media-icons/miro.svg";
import blender from "@styles/assets/images/hirescreens/media-icons/blender.svg";
import premier_pro from "@styles/assets/images/hirescreens/media-icons/premier_pro.svg";
import nuke from "@styles/assets/images/hirescreens/media-icons/nuke.svg";
import after_effects from "@styles/assets/images/hirescreens/media-icons/after_effects.svg";
import moho from "@styles/assets/images/hirescreens/media-icons/moho.svg";
import d_max from "@styles/assets/images/hirescreens/media-icons/3d_max.svg";

import affinity from "@styles/assets/images/hirescreens/hire-graphic-designers/vscode-icons_file-type-affinitydesigner.svg";
import figma from "@styles/assets/images/hirescreens/hire-product-designers/figma.svg";
import illustrator from "@styles/assets/images/hirescreens/hire-graphic-designers/adobe_illustrator.svg";
import Photoshop from "@styles/assets/images/hirescreens/hire-graphic-designers/adobe_photoshop.svg";

import Ticker from "@components/common/Ticker";

const datas = [
  { id: 0, title: "Illustrator", icon: illustrator },
  { id: 1, title: "Photoshop", icon: Photoshop },
  { id: 2, title: "Blender", icon: blender },
  { id: 3, title: "Miro", icon: miro },
  { id: 4, title: "Adobe Premiere Pro", icon: premier_pro },
  { id: 5, title: "Figma", icon: figma },
  { id: 6, title: "Nuke", icon: nuke },
  { id: 7, title: "Affinity Designer", icon: affinity },
  { id: 8, title: "Adobe After Effects", icon: after_effects },
  { id: 9, title: "Moho", icon: moho },
  { id: 10, title: "3ds Max", icon: d_max },
];
const TechStackMarquee = () => {
  const [scrollDir, setScrollDir] = useState(null);

  useEffect(() => {
    let scrollSide = window.scrollY;

    const handleScroll = () => {
      let currentScrollSide = window.scrollY;

      if (currentScrollSide > scrollSide) {
        setScrollDir("down");
      } else if (currentScrollSide < scrollSide) {
        setScrollDir("up");
      }
      scrollSide = currentScrollSide;
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <section className="hire-marquee-wrapper">
      <div className="hire-developers-hero-wrapper item-center gap-lg">
        <div className="item-center gap-lg">
          <h2 className="about-h2">Our Technology Stack</h2>
          <p className="about-p c-text-center">
            Our Motion Designers work with industry-leading animation and
            compositing tools to bring visuals to life—delivering dynamic,
            high-quality motion graphics for web, social, and product visuals.
          </p>
        </div>
        <section className="token-dev-technology-marquee-section">
          <Ticker
            duration={30}
            direction={`${scrollDir === "up" ? 100 : -100}`}
          >
            {datas.map((item) => (
              <div
                className="token-dev-technology-card item-center"
                key={item.id}
              >
                <div className="flex-center">
                  <img src={item.icon} alt={item.title} />
                  <p className="about-p">{item.title}</p>
                </div>
              </div>
            ))}
          </Ticker>
          <Ticker
            duration={30}
            direction={`${scrollDir === "up" ? -100 : 100}`}
          >
            {datas.map((item) => (
              <div
                className="token-dev-technology-card item-center"
                key={item.id}
              >
                <div className="flex-center">
                  <img src={item.icon} alt={item.title} />
                  <p className="about-p">{item.title}</p>
                </div>
              </div>
            ))}
          </Ticker>
        </section>
      </div>
    </section>
  );
};

export default TechStackMarquee;
